import React, { Component } from 'react';
import { Col, Form, Input, Select, DatePicker } from 'antd';
import moment from 'moment';

@Form.create({ name: 'FormInfoBasic' })
class FormInfoBasic extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      // labelCol: {
      //   xs: { span: 24 },
      //   sm: { span: 6 },
      // },
      // wrapperCol: {
      //   xs: { span: 24 },
      //   sm: { span: 18 },
      // },
      // colon: false,
    };
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={24}>
          <Form.Item {...formItemLayout} label="Ngày sinh">
            {getFieldDecorator('system1', { initialValue: moment('1994/03/08') })(
              <DatePicker style={{ width: '100%' }} />
            )}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item {...formItemLayout} label="Giới tính">
            {getFieldDecorator('system2', { initialValue: 'Nam' })(
              <Select>
                <Select.Option value="name">Nam</Select.Option>
                <Select.Option value="nu">Nữ</Select.Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item {...formItemLayout} label="ĐT cá nhân">
            {getFieldDecorator('system3', { initialValue: '096843442' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item {...formItemLayout} label="Email">
            {getFieldDecorator('system5', { initialValue: 'toilatoi@gmail.com' })(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoBasic;



