import React, { Component } from 'react';
import { Col, Form, Input, Select, DatePicker } from 'antd';
import moment from 'moment';

const createSelect = name => (
  <Select>
    {[1, 2, 3].map(item => (
      <Select.Option value={item}>{`${name} ${item}`}</Select.Option>
    ))}
  </Select>
);
@Form.create({ name: 'FormInfoBasic' })
class FormInfoBasic extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = null;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="CMND">
            {getFieldDecorator('system1', { initialValue: '637259173' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Nơi cấp">
            {getFieldDecorator('system2', { initialValue: 'Bình Định' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Ngày cấp">
            {getFieldDecorator('system3', { initialValue: moment('2010/10/05') })(
              <DatePicker style={{ width: '100%' }} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Dân tộc">
            {getFieldDecorator('system4', { initialValue: 'Kinh' })(createSelect('Dân tộc'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Tôn giáo">
            {getFieldDecorator('system5', { initialValue: 'Không' })(createSelect('Tôn giáo'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Đoàn">
            {getFieldDecorator('system6', { initialValue: moment('2011/03/26') })(
              <DatePicker style={{ width: '100%' }} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Đảng">
            {getFieldDecorator('system7', { initialValue: '' })(
              <DatePicker style={{ width: '100%' }} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="ĐT chính sách">
            {getFieldDecorator('system8', { initialValue: '' })(createSelect('ĐT chính sách'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="ĐT TC">
            {getFieldDecorator('system9', { initialValue: '' })(createSelect('ĐT TC'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Điện thoại nhà riêng">
            {getFieldDecorator('system10', { initialValue: '0773112' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Nhóm đối">
            {getFieldDecorator('system11', { initialValue: '' })(createSelect('Nhóm đối'))}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoBasic;



