import React, { Component } from 'react';
import { Col, Form, Input, Select } from 'antd';

const createSelect = name => (
  <Select>
    {[1, 2, 3].map(item => (
      <Select.Option value={item}>{`${name} ${item}`}</Select.Option>
    ))}
  </Select>
);

@Form.create({ name: 'FormInfoAddrBasic' })
class FormInfoAddrBasic extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      // labelCol: {
      //   xs: { span: 24 },
      //   sm: { span: 6 },
      // },
      // wrapperCol: {
      //   xs: { span: 24 },
      //   sm: { span: 18 },
      // },
      // colon: false,
    };
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quê quán">
            {getFieldDecorator('system1', { initialValue: 'Bình Định' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quốc tịch">
            {getFieldDecorator('system2', { initialValue: 'Việt Nam' })(createSelect('Quốc tịch'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="TP xuất thân">
            {getFieldDecorator('system3', { initialValue: 'Qui Nhơn' })(createSelect('Thành phố'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="ĐC thường trú">
            {getFieldDecorator('system4', { initialValue: '142 Chu Văn An' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Xã/Phường">
            {getFieldDecorator('system5', { initialValue: 'Phường 10' })(createSelect('Xã'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Tỉnh/Tp">
            {getFieldDecorator('system6', { initialValue: 'Qui Nhơn' })(createSelect('Tỉnh'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Quận/Huyện">
            {getFieldDecorator('system7', { initialValue: 'Phù Mỹ' })(createSelect('Huyện'))}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Địa chỉ báo tin">
            {getFieldDecorator('system8', { initialValue: '143 An Dương Vương' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item {...formItemLayout} label="Nơi ở hiện nay">
            {getFieldDecorator('system9', { initialValue: '874 Trần Văn Đang' })(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoAddrBasic;



